import React from "react";
import { Nav, NavDropdown } from "react-bootstrap";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useRouteMatch,
  useParams
} from "react-router-dom";

import './Menu.css';
import About from '../about/About';
import Home from '../home/Home';
import Gallery from '../gallery/Gallery';
import NotFound from '../not-found/NotFound';

const Menu = () => {
  const handleSelect = (eventKey) => alert(`selected ${eventKey}`);

  return (<Router>

    <Nav variant="pills" activeKey="1" onSelect={handleSelect}>
      <Nav.Item>
      <Nav.Link as={Link} to="/home">Home</Nav.Link>
      </Nav.Item>
      <NavDropdown title="animales" id="nav-dropdown">
        <NavDropdown.Item as={Link} to="/gallery">Perros</NavDropdown.Item>
        <NavDropdown.Item eventKey="cats">Gatos</NavDropdown.Item>
      </NavDropdown>
      <NavDropdown title="cars" disabled id="nav-dropdown">
        <NavDropdown.Item eventKey="car_1">Coche 1</NavDropdown.Item>
        <NavDropdown.Item eventKey="car_2">Coche 2</NavDropdown.Item>
        <NavDropdown.Item eventKey="car_3">Coche 3</NavDropdown.Item>
      </NavDropdown>
      <Nav.Item>
        <Nav.Link as={Link} to="/about">About</Nav.Link>
      </Nav.Item>
    </Nav>
    <Switch>
      <Route path="/home">
        <Home />
      </Route>
      <Route path="/gallery">
        <Gallery />
      </Route>
      <Route path="/about">
        <About />
      </Route>
      <Route >
        <NotFound />
      </Route>
    </Switch>
  </Router>
  );
};


export default Menu;